<template>
<div id="sales_statistics">
  <div class="tit">销售统计</div>
  <div class="shuj_bnox">
   <div class="lans_calse"><span>{{statistics_total.total_profit}}</span><span>历史扣手续费收益</span></div>
   <div class="shuj_j_npx">
    <div><img src="/static/icon/8.png"><span>{{statistics_total.total_money}}</span><br><span>总提交金额</span></div>
    <div><img src="/static/icon/8.png"><span>{{statistics_total.total_success_money}}</span><br><span>总支付金额</span></div>
    <div><img src="/static/icon/12.png"><span>{{statistics_total.total_success_count}}</span><br><span>总支付订单</span></div>
    <div><img src="/static/icon/12.png"><span>{{statistics_total.total_count}}</span><br><span>总提交订单</span></div>
   </div>
  </div>
  <div data-v-eea61ecc id="echarts_s">
  		<ve-line :data="chartData" :settings="chartSettings"  :extend="charts" :loading="chartloading" style="width:95%;; height: 350px; -webkit-tap-highlight-color: transparent; user-select: none; position: relative;" id="myChart"></ve-line>
        <span data-v-eea61ecc="" class="xy_name">日期</span>
        <span data-v-eea61ecc="" class="xy_name">订单数</span>
        <span data-v-eea61ecc="" class="xy_name">金额</span>
        <div data-v-eea61ecc="" class="type_qie">
	        <span data-v-eea61ecc="" :class="{'xz':chartstime == 7}" @click="getstatistics(7)">一周</span>
	        <span data-v-eea61ecc="" :class="{'xz':chartstime == 10}" @click="getstatistics(10)">十天</span>
	        <span data-v-eea61ecc="" :class="{'xz':chartstime == 15}" @click="getstatistics(15)">半月</span>
        </div>
  </div>
</div>
</template>
<script>
 export default{
    data(){
      this.chartSettings = {
        axisSite: { right: ['提交金额'] },
        xAxisType: 'category',
        yAxisType: ['normal', 'normal'],
      }
      this.chartSettingss = {
        radius: [55,90],
        offsetY: 150
      }
      return {
	      chartData: {
	          columns: ['日期', '提交订单', '提交金额','成功订单','成功金额','总手续费','当日收益'],
	          rows:[]
	        },
	        charts:{
	          backgroundColor: 'rgb(255, 255, 255)',
	          textStyle:{
	             color:'rgb(104, 114, 140)',
	             fontFamily: "Microsoft YaHei",
	             fontSize:10,
	             lineHeight:25
	          },
	          xAxis:{
	            splitLine:{
	              show:true,
	              lineStyle:{
	                 color:['rgba(223, 224, 232, 0.9)'],
	                 type:'dashed'
	              }
	            }
	          },
	          yAxis:{
	            splitLine:{
	              show:true,
	              lineStyle:{
	                 color:['rgba(223, 224, 232, 0.9)'],
	                 type:'dashed'
	              }
	            }
	          },
	          tooltip:{
	             padding: 10,
	             backgroundColor:'rgb(255, 255, 255)',
	             borderColor:'rgb(51, 51, 51)',
	             textStyle:{
	              color:'rgb(104, 114, 140)',
	              fontFamily:'Microsoft YaHei',
	              fontSize:12
	             },
	             extraCssText: 'box-shadow: rgba(80, 125, 250, 0.06) 0px 4px 8px 0px;border-radius: 5px;line-height:25px;'
	          },
	          legend:{
	              data: [{
	                  name: '提交订单',
	                  icon: 'roundRect',
	                  textStyle: {
	                      color: '#a2a9ba'
	                  }
	              },{
	                  name: '提交金额',
	                  icon: 'roundRect',
	                  textStyle: {
	                      color: '#a2a9ba'
	                  }
	              },{
	                  name: '成功订单',
	                  icon: 'roundRect',
	                  textStyle: {
	                      color: '#a2a9ba'
	                  }
	              },{
	                  name: '成功金额',
	                  icon: 'roundRect',
	                  textStyle: {
	                      color: '#a2a9ba'
	                  }
	              },{
	                  name: '总手续费',
	                  icon: 'roundRect',
	                  textStyle: {
	                      color: '#a2a9ba'
	                  }
	              },{
	                  name: '当日收益',
	                  icon: 'roundRect',
	                  textStyle: {
	                      color: '#a2a9ba'
	                  }
	              }],
	              backgroundColor:'rgb(255, 255, 255)',
	              borderColor:'rgb(51, 51, 51)',
	          }
	        },
	        chartDatas: {
	          columns: ['支付方式', '支付金额'],
	          rows:[]
	        },
	        chartss:{
	          tooltip:{
		             padding: 10,
		             backgroundColor:'rgb(255, 255, 255)',
		             borderColor:'rgb(51, 51, 51)',
		             textStyle:{
		              color:'rgb(104, 114, 140)',
		              fontFamily:'Microsoft YaHei',
		              fontSize:12
		             },
		             extraCssText:'box-shadow: rgba(80, 125, 250, 0.06) 0px 4px 8px 0px;border-radius: 5px;line-height:25px;'
		          },
	        	legend:{
	            show:false
	          },
	          series:{
	            label: {
	              show:false
	            }
	          }
	        },
	        statistics_total:"",
          chartstime:7,
          time:'',
	        chartloading:true,
      }
    },
    mounted(){
      this.getstatistics(7);
    },
	methods:{
		 getstatistics(time){
       this.chartstime = time;
		   if(localStorage.getItem('user_statistics_'+time)){
		     var statistics = JSON.parse(localStorage.getItem('user_statistics_'+time));
		     if(statistics.time < new Date().getTime()){
		       this.ajaxstatistics(time);
		     }else{
		       this.chartData.rows = statistics.data.chart;
		       this.chartloading = false;
		       this.statistics_total = statistics.data.Total;
		     }
		   }else{
		     this.ajaxstatistics(time);
		   }
		 },
		 ajaxstatistics(time){
		    this.$http.post('api/user/statistics',{time:time}, res => {
		         if (res.status == 200) {
		             if(res.data.code == 200){
		               var local = {};
		                   local.time = new Date().getTime() + 50000;
		                   local.data = res.data.data;
		               localStorage.setItem('user_statistics_'+time,JSON.stringify(local));
		               this.chartData.rows = local.data.chart;
		               this.chartloading = false;
		               this.statistics_total = local.data.Total;
		             }else if(res.data.code == 201){
		               this.$Message.warning(res.data.msg);
		             }
		         } else {
		            console.log(res.message);
		         }
		    });
		 }
	}

 }
</script>



